body{
  height: 400px;
}

#form{
  background-color: #fff;
  width: 300px;
  padding: 20px 15px;
  margin: auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 20px;
  box-shadow: 0px 2px 10px 0px #dadada;
  color: #8d8d8d;

  h1{
    text-align: center;
    color: inherit;
    padding-bottom: 15px;
    // color: #783ee5;
  }
  form{
    position: relative;
    .input-group{
      border-bottom: 0.8px solid #dddddd;
      
      &:nth-child(1){
        margin-bottom: 15px;
      }
      > *{
        display: inline-block;
        height: 100%;
      }
      .icon{
        width: 20px;
        font-size: 0.8em;
      }
  
      input{
        // border: 1px solid black;
        width: 80%;
        color: inherit;

        &:placeholder-shown,
        &::placeholder,
        &::-webkit-input-placeholder{
          color: inherit;
        }
      }
    }

    button{
      color: white;
      background-image: linear-gradient(to right bottom, #249bcf, #1ca1db, #13a6e7, #08acf3, #00b1ff);
      width: 50%;
      font-size: .9em;
      font-weight: bold;
      padding: 12px 24px;
      border-radius: 22px;
      margin: auto;
      display: block;
      position: absolute;
      left: 50%;
      // bottom: 50%;
      transform: translateX(-50%)
    }

    > *{
      padding: 6px 12px;
    }

    .link{
      margin-bottom: 15px;
    }

    .note{
      font-size: 0.8em;
      margin-bottom: 15px;


      a{
        font-size: 1em;
        // text-decoration: underline;
        color: #249bcf;

      }
    }

    
  }
}



