// normalize
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: inherit
}


body{
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  button, input{
    border: none;
    outline: 0;
    padding: 6px 12px;
  }

  button, input, a{
    font-size: 0.8em;
  }

  a{
    text-decoration: none;
    // color: rgb(38, 202, 251);
  }

  ul{
    list-style: none;
  }

}